@import url(https://fonts.googleapis.com/css2?family=Zilla+Slab:ital,wght@0,300;0,400;0,500;0,600;0,700;1,400&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+TC:wght@400;500;700&display=swap);
html {
	margin: 0;
	padding: 0;
	font-size: 62.5%;
}

body {
	margin: 0;
  padding: 0;
  font-family: 'Zilla Slab', serif;
}

.header {
	height: 15rem;
	width: 100vw;
	margin-bottom: 0;
	padding: 0;
	background-color: #aaa;
}

.menuBtn {
	height: 1.8rem;
	margin-top: 3rem;
  cursor: pointer;
  padding: .7rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.line {
	height: .2rem;
	width: 1.5rem;
  background-color: #222;
  transition: all .2s;
}

.line-1-closed {
	width: 1.5rem;
	transition: all .2s;
}

.line-2-closed {
	width: 2rem;
	transition: all .2s;
}

.line-3-closed {
	width: 2.5rem;
	transition: all .2s;
}

.menuBtn:hover .line-1-closed, 
.menuBtn:hover .line-2-closed {
	width: 2.5rem;
}

.line-1-open {
	width: 2.5rem;
	transform: rotateZ(225deg);
	position: relative;
	top: 47%;
}

.line-2-open {
	display: none;
}

.line-3-open {
	width: 2.5rem;
	transform: rotateZ(-225deg);
	position: relative;
	top: -47%;
}

.sidebar {
	background-color: #ccc;
	width: 8rem;
	height: 100vh;
	position: fixed;
	top: 0;
	left: 0;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	align-items: center;
	z-index: 100;
}

.social-list {
	list-style-type: none;
	margin: 0;
	padding: 0;
	height: 20rem;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
}

.social-links {
	font-size: 3rem;
}

.sidebar-date {
	font-size: 1.7rem;
	margin-bottom: 1rem;
}

.nav-menu {
	width: 40rem;
	height: 100vh;
	background-color: rgba(0, 0, 0, 0.9);
	position: fixed;
	top: 0;
	left: -32rem;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	align-items: center;
	transition: all .3s ease-in-out;
	box-shadow: .2rem .2rem 1rem rgba(0, 0, 0, .3);
	z-index: 50;
}

.nav-menu.open {
	left: 8rem;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
}

.nav-list-container {
	width: 100%;
	height: 50%;
	margin-top: 12rem;
}

.nav-list {
	height: 100%;
	font-family: 'Noto Sans TC', sans-serif;
	font-size: 3.5rem;
	text-align: right;
	color: #ddd;
	list-style-type: none;
	margin: 0;
	padding-right: 5rem;
	display: flex;
	flex-direction: column;
	justify-content: space-evenly;
}

.nav-list a {
	text-decoration: none;
}

.nav-list a:link {
	color: #ddd;
}

.nav-list a:visited {
	color: #ddd;
}

.nav-list a:hover {
	
}

.personal-info {
	color: #fff;
	font-size: 1.5rem;
	text-align: center;
	display: flex;
	flex-direction: column;
}

.personal-info span {
	margin-bottom: .4rem;
}

.searchbox {
	width: 35rem;
	height: 4rem;
	font-size: 2rem;
	margin-bottom: 2rem;
	outline: none;
}


.projects-content {
	text-align: center;
}

.projects-heading {
	margin: 0 0 2rem 0;
	font-size: 3.7rem;
	font-weight: 400;
}
.content-container {
	background-color: #ccc;
	position: fixed;
	top: 15rem;
	right: 0;
	bottom: 7.3rem;
	left: 8rem;
	padding: 1.5rem;
}
.backdrop {
	width: 100vw;
	height: 100vh;
	position: fixed;
	top: 0;
	left: 0;
	background-color: #000;
	opacity: 0;
	z-index: 25;
	display: none;
}

.open {
	display: block;
	opacity: .8;
}

.footer {
	width: 100vw;
	height: 7.3rem;
	color: #000;
	font-size: 1.5rem;
	background-color: #aaa;
	position: fixed;
	bottom: 0;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
}

p {
	margin: 0;
	padding: 0;
}

.cr {
	font-size: 2rem;
}

.react {
	color: #61DBFB;
	background-color: #444;
	padding: .2rem .3rem;
}

.App {
  margin: 0;
  padding: 0;
}

